<template>
  <div class="page-view no-header">
    <section class="course-img" >
      <img :src="baseurl+course.course_img" class="responsive-img" alt="">
      <navbar :bgcolor="'rgba(0,0,0,0)'" :nobg="true"></navbar>
    </section>
    <section class="course-title">
      <h4 class="name">{{course.title}}</h4>
      <div class="author">
          {{course.teacher&&course.teacher.name}}
      </div>
    </section>
    <section class="table course-start" v-if="course.right==1">
      <div class="">
        您已报名 <span class="day">{{course.study_time}}天</span>
      </div>
      <a :href="'#/video/'+course.cid" class="start-btn">
        开始学习
      </a>
    </section>
    <section class="table course-start" v-if="course.right==0">
      <div class="">
        <div class="money">￥{{course.price}}</div>
        <div class="small">上课时间：{{course.study_time}}</div>
      </div>
      <a :href="'#/video/'+course.cid" class="start-btn">
        开始报名
      </a>
    </section>
    <yd-accordion class="course-accordion">
      <yd-accordion-item title="课程介绍">
        <div class="course-content" v-html="course.introduce">

        </div>
        </yd-accordion-item>
      <yd-accordion-item title="讲师介绍">
        <div class="course-content">
           <yd-flexbox class="course-teacher" v-if="course.teacher">
             <yd-flexbox-item class="img">
               <img  :src="baseurl+course.teacher.pic" alt="">
             </yd-flexbox-item>
             <yd-flexbox-item>
               <h4>{{course.teacher.name}}</h4>
               <p>{{course.teacher.tintroduce}}</p>
             </yd-flexbox-item>
           </yd-flexbox>
        </div>
      </yd-accordion-item>
      <yd-accordion-item title="课程大纲">
        <div class="course-content course-flow" v-html="course.show">

        </div>
        <!--<ul class="course-flow">
          <li class="item">
            <h4 class="title">1、机器人</h4>
            <div class="content">课程内容</div>
          </li>
          <li class="item">
            <h4 class="title">1、机器人</h4>
            <div class="content">课程内容</div>
          </li>
          <li class="item">
            <h4 class="title">1、机器人</h4>
            <div class="content">课程内容</div>
          </li>
        </ul>-->
      </yd-accordion-item>
      <div class="accordion-title text-center"><span>课程介绍</span> </div>
      <!--<list></list>-->
      </yd-accordion>
       <search-bar></search-bar>
  </div>
</template>

<script>
  import List from './templates/CouseList1.vue'
  export default{
    data(){
      return{
        course:{},
        id:this.$route.params.id
      }
    },
    components: {
      List
    },
    created(){
      var self=this;
      this.$api.cintroduce({cid:this.id}).then(function (res) {
          if(res.data&&res.data.code==1){
            self.course=res.data.msg
          }
      })
    }
  }
</script>

<style  lang="less" rel="stylesheet/less">
  @import "../../style/config.less";
  .course-img{
    position: relative;
  }
  .course-title{
    padding: 5px 10px;
    .name{
      font-size: 18px;
      line-height: 1.5;
    }
    .author{
      font-weight: bold;
      margin-bottom: 10px;
      text-align: right;
    }
  }
  .course-start {
    height:60px;
    .money{
      color:orange;
      font-size: 16px;
    }
    .start-btn{
      background-color: @skin-color;
      color:#fff;
      font-size: 16px;
    }
    .day{
      padding: 2px;
      background-color: @skin-color;
      border-radius: 5px;
    }
  }
  .course-accordion .accordion-title{
    min-height: 1.2rem;
  }

  .course-content{
    padding: 20px;
    line-height: 1.5;
  }
